import Box from "@mui/material/Box";
import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import {message} from "antd"
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import {TablePagination} from "@mui/material";
import moment from "moment/moment";


export default function SalesSummary({userId, setUserId, bodyhandler, tokenHandler, tokenSetter}){
    const navigate = useNavigate();
    const [summarys, setSummarys] = useState([]);
    const [total, setTotal] = useState(0);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);

    const url = `/app-api/cinema/app/statistics/page?pageNo=${encodeURIComponent(page + 1)}
                            &pageSize=${encodeURIComponent(rowsPerPage)}`;

    const sendRequest = () =>{
        const accessToken = localStorage.getItem('accessToken');
        fetch(url, {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                "tenant-id": 1,
                "Authorization":`Bearer ${accessToken}`,
            },
        })
            .then(res => res.json())
            .then((res) =>{
                bodyhandler(res);
                if (res.code === 0) {
                    setTotal(res.data.total);
                    const summaryArr = res.data.list.map((item) =>{
                        return {
                            session_id: item.id,
                            movie_name: item.movieName,
                            show_time: item.showTime,
                            saled_ticket_num: item.amount,
                            income: item.quality,
                        };
                    });
                    console.log(summaryArr);
                    setSummarys(summaryArr);
                } else if(res.code === 401) {
                    console.log("Access token...")
                    tokenHandler()
                        .then((data) =>{
                            console.log(data.data);
                            tokenSetter(data.data.userId, data.data.accessToken,
                                data.data.refreshToken, data.data.expiresTime);
                            setUserId(data.data.userId);
                            return sendRequest();
                        });
                } else {
                    throw Error(res.msg);
                }
            })
            .catch((err) => {
                message.error(err.message, 3);
                window.localStorage.setItem('userId', "-1");
                navigate("/index.html#/signin");
            });
    }

    useEffect(() => {
        if(userId === -1){
            message.error("");
            navigate("/index.html#/signin");
            return;
        }
        sendRequest();
    }, [setSummarys, navigate, page, rowsPerPage])

    return(
            <TableContainer component={Paper}>
                <Table sx={{minWidth:650}} aria-label="order list">
                    <TableHead>
                        <TableRow>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {summarys.map((summary) => (
                            <TableRow
                                key={summary.session_id}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="order">{summary.session_id}</TableCell>
                                <TableCell align="left">{summary.movie_name}</TableCell>
                                <TableCell align="left">{moment(parseInt(summary.show_time)).format("YYYY-MM-DD HH:mm:ss")}</TableCell>
                                <TableCell align="left">{summary.saled_ticket_num}</TableCell>
                                <TableCell align="left">{summary.income}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                    <TablePagination count={total} page={page} rowsPerPage={rowsPerPage}
                                     onPageChange={(e, newPage) => {
                                         setPage(newPage);
                                     }}
                                     onRowsPerPageChange={(e, newRowsPerPage) => {
                                         setRowsPerPage(parseInt(e.target.value, 10));
                                         setPage(0);
                                     }}/>
                </Table>
            </TableContainer>
    );
}
