import {useEffect, useRef, useState} from "react";
import {useNavigate} from "react-router-dom";
import {message} from "antd"
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import {TablePagination, TextField} from "@mui/material"
import Button from "@mui/material/Button"
import moment from 'moment';


export default function ScheduleList({userId, setUserId, bodyhandler, tokenHandler, tokenSetter}){
    const [movies, setMovies] = useState([]);
    const [ticket_num, setTicket_num] = useState([]);
    const [total, setTotal] = useState(0);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const navigate = useNavigate();
    const expiresTime = parseInt(localStorage.getItem('expiresTime'));
    const url = `/app-api/cinema/app/schedule/list?pageNo=${encodeURIComponent(page + 1)}&pageSize=${encodeURIComponent(rowsPerPage)}`;

    const ticket_numIsValid = (value, index) =>{
        const check = ticket_num[index];
        return (check === undefined) || (check !== "0" && check <= value && check >= 1);
    };

    const ticket_numChangeHandler = (e, index) => {
        const arr = [...ticket_num];
        arr[index] = e.target.value;
        setTicket_num(arr);
    };

    const submitOrder = (session_id, remain_ticket_num, index) => {
        if (!ticket_numIsValid(remain_ticket_num, index) || typeof ticket_num[index] == "undefined" || ticket_num[index] === ""|| userId === -1) {
            if(userId === -1){
                message.error("");
            }else{
                message.error(",");
            }
            const arr = [...ticket_num];
            arr[index] = "";
            setTicket_num(arr);
            return;
        }
        const sendRequest = () =>{
            const accessToken = localStorage.getItem('accessToken');
            fetch(`/app-api/cinema/app/order/create`, {
                method: "POST",
                body: JSON.stringify({
                    scheduleId: session_id,
                    customerId: userId,
                    quality: ticket_num[index],
                }),
                headers: {
                    "Content-Type": "application/json",
                    "tenant-id": 1,
                    "Authorization":`Bearer ${accessToken}`,
                },
            })
                .then(res => res.json())
                .then((res) => {
                    bodyhandler(res);
                    if (res.code === 0) {
                        const updatedMovies = [...movies]
                        updatedMovies[index].remain_ticket_num -= ticket_num[index];
                        setMovies(updatedMovies)
                        message.success("")
                    } else if(res.code === 401) {
                        message.warning("");
                        getList(() => {
                            const arr = [...ticket_num];
                            arr[index] = "";
                            setTicket_num(arr);
                        });
                    }else {
                        throw Error(res.msg);
                    }
                })
                .catch((err) => {
                    message.error(err.message);
                    window.localStorage.setItem('userId', "-1");
                });
        }
        sendRequest();
    }
    const getList = (callback) =>{
        fetch(url, {
            method: "GET",
            headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                "tenant-id": 1,
            },
        })
            .then(res => res.json())
            .then((res) =>{
                bodyhandler(res);
                if (res.code === 0) {
                    return res;
                } else {
                    throw Error(res.msg);
                }
            })
            .then((data) => {
                setTotal(data.data.total);
                return data.data.list.map((item) => {
                    return {
                        session_id: item.sessionId,
                        theater_name: item.theaterName,
                        movie_name: item.movieName,
                        show_time: item.showTime,
                        remain_ticket_num: item.quota,
                        cost: item.price,
                    };
                });
            })
            .then((movieArr) =>{
                console.log(movieArr);
                setMovies(movieArr);
            })
            .then(() => {
                if(callback){
                    callback();
                }
            })
            .catch((err) => {
                message.error(err.message, 3);
            });
    }

    useEffect(() => {
        getList(() =>{
            setTicket_num([]);
        });
    }, [setMovies, navigate, page, rowsPerPage])

    useEffect(() => {
        if(new Date().getTime() > expiresTime){
            console.log("Access Token");
            if(userId !== -1){
                tokenHandler()
                    .then((data) => {
                        console.log(data.data);
                        tokenSetter(data.data.userId, data.data.accessToken, data.data.refreshToken, data.data.expiresTime);
                        console.log("Access Token");
                    })
                    .catch((err) => {
                        message.error(err.message, 3);
                        window.localStorage.setItem('userId', "-1");
                        setUserId(-1);
                    });
            }
        }else{
            console.log("Access Token");
        }
    },[navigate])


    return(
            <TableContainer component={Paper}>
                <Table sx={{minWidth:650}} aria-label="order list">
                    <TableHead>
                        <TableRow>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {movies.map((movie, index) => (
                            <TableRow
                                key={movie.session_id}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="order">{movie.session_id}</TableCell>
                                <TableCell align="left">{movie.theater_name}</TableCell>
                                <TableCell align="left">{movie.movie_name}</TableCell>
                                <TableCell align="left">{moment(parseInt(movie.show_time)).format("YYYY-MM-DD HH:mm:ss")}</TableCell>
                                <TableCell align="left">{movie.remain_ticket_num}</TableCell>
                                <TableCell align="left">{movie.cost}</TableCell>
                                <TableCell>
                                    <TextField
                                        fullWidth
                                        id="ticket_num"
                                        name="ticket_num"
                                        helperText=""
                                        onChange={(e)=>ticket_numChangeHandler(e, index)}
                                        error={!ticket_numIsValid(movie.remain_ticket_num, index)}
                                        value={ticket_num[index] || ''}>
                                    </TextField>
                                </TableCell>
                                <TableCell>
                                    <Button
                                        variant="contained"
                                        onClick={()=>submitOrder(movie.session_id, movie.remain_ticket_num, index)}
                                    >
                                        
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                    <TablePagination count={total} page={page} rowsPerPage={rowsPerPage}
                                     onPageChange={(e, newPage) => {
                                         setPage(newPage);
                                     }}
                                     onRowsPerPageChange={(e, newRowsPerPage) => {
                                         setRowsPerPage(parseInt(e.target.value, 10));
                                         setPage(0);
                                     }}/>
                </Table>
            </TableContainer>
    );
}
