import React, {useContext, useEffect, useState} from "react";
import { Button, Typography } from "@mui/material";
import {useNavigate} from "react-router-dom";
import { Box, Container } from "@mui/material";

import { message } from "antd"

const dFlex = {
    display: "flex",
    flexDirection: "row",
};
const flexBetweenCenter = {
    display: "flex",
    justifyContent: { xs: "center", md: "space-between" },
    alignItems: "center",
};

export default function Layout({userId, setUserId}) {
    const navigate = useNavigate();
    const accessToken = localStorage.getItem('accessToken');
    const expiresTime = parseInt(localStorage.getItem('expiresTime'));
    const logOutHandler = () => {
        if(expiresTime <= new Date().getTime()){
            window.localStorage.setItem('userId', "-1");
            message.info("").then(() => navigate("/scheduleList"));
            // navigate("/scheduleList");
        }else if (window.confirm("")){
            window.localStorage.setItem('userId', "-1");
            window.localStorage.setItem('expiresTime', "-1");
            fetch("/app-api/member/auth/logout", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "tenant-id": 1,
                    "Authorization":`Bearer ${accessToken}`
                },
            })
                .then(() => {
                    navigate("/scheduleList");
                    message.info("");
                    setUserId(-1);
                    console.log(userId);
                });
        }
    };

    return (
        <>
            <Box
                sx={{
                    ...dFlex,
                    minHeight: 70,
                    borderBottom: "1px solid #ddd",
                }}
            >
                <Container maxWidth="2500px">
                    <Box
                        sx={{
                            ...flexBetweenCenter,
                            minHeight: 70,
                            px: 8,
                        }}
                    >
                        <div>
                            <Button
                                href="/index.html#/orderList"
                                disableRipple
                                variant="text"
                                size="large"
                                sx={{
                                    "&:hover": {
                                        backgroundColor: "#D5D5D5",
                                        boxShadow: "none",
                                    },
                                }}
                            >
                                <Typography
                                    color="#010101"
                                    component={"div"}
                                    sx={{ fontWeight: "bold" }}
                                >
                                    
                                </Typography>
                            </Button>
                            <Button
                                href="/index.html#/scheduleList"
                                disableRipple
                                variant="text"
                                size="large"
                                sx={{
                                    "&:hover": {
                                        backgroundColor: "#D5D5D5",
                                        boxShadow: "none",
                                    },
                                }}
                            >
                                <Typography
                                    color="#010101"
                                    component={"div"}
                                    sx={{ fontWeight: "bold" }}
                                >
                                    
                                </Typography>
                            </Button>
                            <Button
                                href="/index.html#/salesSummary"
                                disableRipple
                                variant="text"
                                size="large"
                                sx={{
                                    "&:hover": {
                                        backgroundColor: "#D5D5D5",
                                        boxShadow: "none",
                                    },
                                }}
                            >
                                <Typography
                                    color="#010101"
                                    component={"div"}
                                    sx={{ fontWeight: "bold" }}
                                >
                                 
                                </Typography>
                            </Button>
                            {userId !== -1 ? <Button
                            onClick={logOutHandler}
                            disableRipple
                            variant="text"
                            size="large"
                            sx={{
                                "&:hover": {
                                    backgroundColor: "#D5D5D5",
                                    boxShadow: "none",
                                },
                            }}
                        >
                            <Typography
                                color="#010101"
                                component={"div"}
                                sx={{fontWeight: "bold"}}
                            >
                                
                            </Typography>
                        </Button> : <Button
                            href="/index.html#/signin"
                            disableRipple
                            variant="text"
                            size="large"
                            sx={{
                                "&:hover": {
                                    backgroundColor: "#D5D5D5",
                                    boxShadow: "none",
                                },
                            }}
                        >
                            <Typography
                                color="#010101"
                                component={"div"}
                                sx={{fontWeight: "bold"}}
                            >
                                
                            </Typography>
                        </Button>
                        }
                        </div>
                    </Box>
                </Container>
            </Box>
        </>
    );
}
