import {Box} from "@mui/material";
import {Route, Routes, useLocation, Navigate} from "react-router-dom";
import Layout from "./components/Layout";
import Signin from "./components/Signin";

import { useState } from "react";
import OrderList from "./components/OrderList"
import SalesSummary from "./components/SalesSummary"
import ScheduleList from "./components/ScheduleList"
import * as PropTypes from "prop-types"

async function bodyHandler(res){
    console.log(res);
}

async function tokenSetter(userId, accessToken, refreshToken, expiresTime){
    window.localStorage.setItem('userId', userId);
    window.localStorage.setItem('accessToken', accessToken);
    window.localStorage.setItem('refreshToken', refreshToken);
    window.localStorage.setItem('expiresTime', expiresTime);
}

const tokenHandler = () =>{
    const refreshToken = localStorage.getItem('refreshToken');
    const url = `/app-api/member/auth/refresh-token?refreshToken=${encodeURIComponent(refreshToken)}`;
    return fetch(url, {
        method: "POST",
        headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
            "tenant-id": 1,
        },
    }).then(res => res.json())
        .then((res) =>{
            console.log(res);
            if (res.code === 0) {
                return res;
            } else {
                throw Error(res.msg);
            }
        });
}


Navigate.propTypes = {to: PropTypes.string}

function App() {
  const [userId, setUserId] = useState(Number(window.localStorage.getItem('userId')));
  const location = useLocation();
  return (
      <Box>
        <Box>
            {!(location.pathname === "/signin") && <Layout userId={userId} setUserId={setUserId}/>}
        </Box>

        <Box>
          <Routes>
            <Route path="/" element={<Navigate to="/scheduleList" />}/>
            <Route path="/signin" element={<Signin userId={userId} setUserId={setUserId}
                                                   bodyhandler={bodyHandler} tokenSetter={tokenSetter}/>} />
            <Route path="/orderList" element={<OrderList userId={userId} setUserId={setUserId}
                                                         bodyhandler={bodyHandler} tokenHandler={tokenHandler}
                                                         tokenSetter={tokenSetter}/>} />
            <Route path="/salesSummary" element={<SalesSummary userId={userId} setUserId={setUserId}
                                                               bodyhandler={bodyHandler} tokenHandler={tokenHandler}
                                                               tokenSetter={tokenSetter}/>} />
            <Route path="/scheduleList" element={<ScheduleList userId={userId} setUserId={setUserId}
                                                               bodyhandler={bodyHandler} tokenHandler={tokenHandler}
                                                               tokenSetter={tokenSetter}/>} />
            <Route path="*" element={<Navigate to="/" />}/>
          </Routes>
        </Box>
      </Box>
  );
}

export default App;
