import {useEffect, useState} from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import { Link as RouterLink, useNavigate } from "react-router-dom";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";

import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";

import FormControl from "@mui/material/FormControl";


import {FormHelperText, Paper} from "@mui/material";
import {message} from "antd"

const theme = createTheme();
let timeChange;
export default function SignIn({userId, setUserId, bodyhandler, tokenSetter}) {
  const navigate = useNavigate();
  const expiresTime = parseInt(localStorage.getItem('expiresTime'));

  const [time, setTime] = useState(60);
  const [btnDisabled, setBtnDisabled] = useState(false);
  const [btnContent, setBtnContent] = useState('');

  const [accountInput, setAccountInput] = useState("");
  const [accountIsTouched, setAccountIsTouched] = useState(false);

  const [codeInput, setCodeInput] = useState("");
  const [codeIsTouched, setCodeIsTouched] = useState(false);

  const accountValidator = /^1([0-9])\d{9}$/;
  const codeValidator = /^[0-9]+$/;

  const accountIsValid = (accountValidator.test(accountInput) && accountInput.length >= 3 && accountInput.length <= 20);
  const accountIsInValid = !accountIsValid && accountIsTouched;

  const codeIsValid = (codeValidator.test(codeInput) && codeInput.length >= 4 && codeInput.length <= 6 );
  const codeIsInValid = !codeIsValid && codeIsTouched;

  const accountChangeHandler = (e) => {
    setAccountInput(e.target.value);
  };
  const accountBlurHandler = () => {
    setAccountIsTouched(true);
  };
  const codeChangeHandler = (e) => {
    setCodeInput(e.target.value);
  };
  const codeBlurHandler = () => {
    setCodeIsTouched(true);
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    setAccountIsTouched(true);
    setCodeIsTouched(true);

    if (!accountIsValid || !codeIsValid) {
      message.error("");
      return;
    }

    fetch("/app-api/member/auth/sms-login", {
      method: "POST",
      body: JSON.stringify({
        mobile: accountInput,
        code: codeInput
      }),
      headers: {
        "Content-Type": "application/json",
        "terminal": 20,
        "tenant-id": 1
      },
    })
      .then(res => res.json())
      .then((res) => {
        bodyhandler(res);
        if (res.code === 0) {
          return res;
        } else {
          throw Error(res.msg);
        }
      })
      .then((data) =>{
        console.log(data.data);
        tokenSetter(data.data.userId, data.data.accessToken,
            data.data.refreshToken, data.data.expiresTime);
        message.success("")
        navigate("/index.html#/scheduleList");
      })
      .catch((err) => {
        message.error(err.message);
      });

    setAccountInput("");
    setCodeInput("");

    setAccountIsTouched(false);
    setCodeIsTouched(false);
  };

  useEffect(() => {
    if(userId !== -1 && expiresTime !== null && expiresTime > new Date().getTime()){
      message.error("")
      console.log(userId)
      navigate("/scheduleList")
    }
    setUserId(localStorage.getItem('userId'));
    clearInterval(timeChange);
    return () => clearInterval(timeChange);
  }, [navigate]);

  useEffect(() => {
    if(time > 0 && time < 60){
      setBtnContent(`${time}s`);
    }else{
      clearInterval(timeChange);
      setBtnDisabled(false);
      setTime(60);
      setBtnContent('');
    }
  }, [time]);

  const getPhoneCaptcha = (event) =>{
    event.preventDefault();

    setAccountIsTouched(true);

    if (!accountIsValid) {
      message.error("");
      return;
    }

    timeChange = setInterval(() => setTime(t => --t), 1000);
    setBtnDisabled(true);
    fetch("/app-api/member/auth/send-sms-code",{
      method: "POST",
      body: JSON.stringify({
        mobile: accountInput,
        scene: 1,
      }),
      headers: {
        "Content-Type": "application/json",
        "tenant-id": 1,
      },
    })
        .then(res => res.json())
        .then((res) => {
          bodyhandler(res);
          if (res.code === 0) {
            return res;
          } else {
            throw Error(res.msg);
          }
        })
        .catch((err) => {
          message.error(err.message);
        });
  }

  return (
    <ThemeProvider theme={theme}>
      <Container
        sx={{ display: "flex", justifyContent: "center", marginTop: 15 }}
      >
        <Paper
          sx={{
            width: { xs: 200, md: 400 },

            borderRadius: 5,

            paddingX: "12px",
          }}
          elevation={3}
        >
          <CssBaseline />
          <Box
            sx={{
              marginTop: 3,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h4">
              
            </Typography>
            <Box
              component="form"
              noValidate
              onSubmit={handleSubmit}
              sx={{ mt: 3 }}
            >
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <TextField
                    required
                    fullWidth
                    id="account"
                    label=""
                    name="account"
                    autoComplete="account"
                    helperText="111"
                    onChange={accountChangeHandler}
                    onBlur={accountBlurHandler}
                    error={accountIsInValid}
                    value={accountInput}
                  />
                </Grid>

                <Grid item xs={12}>
                  <FormControl fullWidth required>
                    <TextField
                      required
                      id="code"
                      name="code"
                      label=""
                      value={codeInput}
                      onChange={codeChangeHandler}
                      onBlur={codeBlurHandler}
                      error={codeIsInValid}
                      helperText="4-6"
                    />
                    <Button disabled={btnDisabled} onClick={getPhoneCaptcha} variant="outlined">
                      {btnContent}
                    </Button>
                  </FormControl>
                </Grid>
              </Grid>

              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
              >
                
              </Button>
            </Box>
          </Box>
        </Paper>
      </Container>
    </ThemeProvider>
  );
}
