import Box from "@mui/material/Box";
import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import {message} from "antd"
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import {TablePagination} from "@mui/material";
import moment from "moment/moment";


export default function OrderList({userId, setUserId, bodyhandler, tokenHandler, tokenSetter}){
    const [orders, setOrders] = useState([]);
    const [total, setTotal] = useState(0);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const navigate = useNavigate();
    const url = `/app-api/cinema/app/order/page?pageNo=${encodeURIComponent(page + 1)}
    &pageSize=${encodeURIComponent(rowsPerPage)}&customerId=${encodeURIComponent(userId)}`;

    useEffect(() => {
        if(userId === -1){
            message.error("");
            navigate("/index.html#/signin");
            return;
        }
        const sendRequest = () =>{
            const accessToken = localStorage.getItem('accessToken');
            fetch(url, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "tenant-id": 1,
                    "Authorization":`Bearer ${accessToken}`,
                },
            })  .then(res => res.json())
                .then((res) =>{
                    bodyhandler(res);
                    if (res.code === 0) {
                        setTotal(res.data.total);
                        const orderArr = res.data.list.map((item) =>{
                            return {
                                order_id: item.id,
                                movie_name: item.movieName,
                                show_time: item.showTime,
                                purchase_time: item.orderTime,
                                ticket_num: item.quality,
                                cost: item.price,
                            };
                        });
                        console.log(orderArr);
                        setOrders(orderArr);
                    } else if(res.code === 401) {
                        console.log("Access token...")
                        tokenHandler()
                            .then((data) =>{
                                console.log(data.data);
                                tokenSetter(data.data.userId, data.data.accessToken,
                                    data.data.refreshToken, data.data.expiresTime);
                                setUserId(data.data.userId);
                                return sendRequest();
                            });
                    } else {
                        throw Error(res.msg);
                    }
                })
                .catch((err) => {
                    message.error(err.message, 3);
                    window.localStorage.setItem('userId', "-1");
                    navigate("/index.html#/signin");
                });
        }
        sendRequest();
    }, [setOrders, navigate, userId])

    return(
            <TableContainer component={Paper}>
                <Table sx={{minWidth:650}} aria-label="order list">
                    <TableHead>
                        <TableRow>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                            <TableCell aligin="left"></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {orders.map((order) => (
                            <TableRow
                                key={order.order_id}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="order">{order.order_id}</TableCell>
                                <TableCell align="left">{order.movie_name}</TableCell>
                                <TableCell align="left">{moment(parseInt(order.show_time)).format("YYYY-MM-DD HH:mm:ss")}</TableCell>
                                <TableCell align="left">{moment(parseInt(order.purchase_time)).format("YYYY-MM-DD HH:mm:ss")}</TableCell>
                                <TableCell align="left">{order.ticket_num}</TableCell>
                                <TableCell align="left">{order.cost}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                    <TablePagination count={total} page={page} rowsPerPage={rowsPerPage}
                                     onPageChange={(e, newPage) => {
                                         setPage(newPage);
                                     }}
                                     onRowsPerPageChange={(e, newRowsPerPage) => {
                                         setRowsPerPage(parseInt(e.target.value, 10));
                                         setPage(0);
                                     }}/>
                </Table>
            </TableContainer>
    );
}
